@font-face {
  font-family: "SecretCode";
  src: url(/static/media/Code-Regular.4987dcae.woff);
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.SecretCode {
  font-family: "SecretCode", serif;
  font-size: larger;
  font-weight: bold;
}

.AppWrapper{display:flex;flex-direction:column;height:100%}
.Loading-Container .loading{width:1em;height:50px;margin:auto;color:transparent;text-shadow:none;background-color:transparent;border:0;position:relative;padding-left:.5em}.Loading-Container .loading:after{content:"";display:block;position:absolute;font-size:10px;width:1em;height:1em;animation:spinner 2s infinite linear;border-radius:.5em;box-shadow:#9679cd 1.5em 0 0 0,#9679cd 1.1em 1.1em 0 0,#9679cd 0 1.5em 0 0,#9679cd -1.1em 1.1em 0 0,#9679cd -1.5em 0 0 0,#9679cd -1.1em -1.1em 0 0,#9679cd 0 -1.5em 0 0,#9679cd 1.1em -1.1em 0 0}@keyframes spinner{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
.Door-Button-Container{width:300px;padding:10px}.Door-Button-Container a{text-decoration:none}.Door-Button-Container a .Door-Button-Door{transition-duration:.5s}.Door-Button-Container a:hover .Door-Button-Door,.Door-Button-Container a:focus .Door-Button-Door{filter:drop-shadow(0 0 5px #e98a81) drop-shadow(0 0 5px #e98a81);transform:scale(0.98)}.Door-Button-Label{background-color:#f4d5a1;text-align:center;margin-top:-6px;padding:8px;font-weight:bold;font-size:larger;border-radius:8px}
.Fireflies-Container{z-index:1;position:absolute;height:100%;overflow:hidden}
.Home-Container{background-image:url(/static/media/sunset-beach.8ca7f506.jpg);background-size:cover;background-position:center center;height:100%;color:#fff}.Home-Container .Snow-Home-Container{overflow:hidden;position:absolute;height:100%;width:100%}.Home-Door-Container{position:relative;display:flex;flex-direction:column;align-items:center;z-index:5}@media(min-width: 800px){.Home-Door-Container{min-height:calc(100% - 80px);width:100%;flex-direction:row;justify-content:space-evenly;align-items:flex-end}}
.Standard-Wrapper{display:flex;flex-direction:column;height:100%;overflow-x:hidden}
.Breadcrumb-Container{left:5px;top:5px;position:absolute;z-index:100;background-color:#fff;padding:10px 20px;border-radius:20px}.Breadcrumb{padding-right:5px}.Breadcrumb:not(:last-of-type):after{content:">";margin-left:5px}
.FooterContainer{background-color:#381223;overflow:hidden;color:#fff;position:relative;min-height:100px}@media screen and (min-width: 550px){.FooterContainer{min-height:68px}}@media screen and (min-width: 700px){.FooterContainer{min-height:58px}}.FooterContainer .CopyrightContainer{text-align:center;transform:rotate(-4deg);margin:20px}@media screen and (max-width: 700px){.FooterContainer .CopyrightContainer{padding-bottom:25px}}.FooterContainer .PrivacyPolicyContainer{position:absolute;right:20px;bottom:20px}.FooterContainer .PrivacyPolicyContainer a{color:#fff}
.HeaderContainer{background-color:#625ace;display:flex;flex-direction:row;justify-content:space-between}.HeaderContainer a{text-decoration:none}.HeaderContainer a .HeaderLogoContainer{display:flex;flex-direction:row;align-items:center;padding:10px;cursor:pointer;transition:text-shadow .5s ease;text-shadow:none}.HeaderContainer a:hover .HeaderLogoContainer,.HeaderContainer a:focus .HeaderLogoContainer{text-shadow:0 0 2px #e98a81}.HeaderNavigationList{list-style:none;display:flex;margin:0 10px;height:100%;align-items:center;justify-content:center;flex-wrap:wrap;padding-left:0}.HeaderTitle{font-size:x-large;margin:10px;color:#fff}@media screen and (max-width: 750px){.HeaderContainer{display:flex;flex-direction:column;align-items:center;padding:10px;cursor:pointer}.HeaderLogoContainer{padding-top:0}.HeaderNavigationList li{padding-bottom:5px}}
.HeaderButtonContainer{text-decoration:none;color:#fff}.HeaderButtonContainer .HeaderButton{border:#f4d5a1 1px solid;border-radius:6px;padding:6px 12px;margin:0 4px;transition:.3s}.HeaderButtonContainer:hover .HeaderButton,.HeaderButtonContainer:focus .HeaderButton{background-color:#542860;cursor:pointer}

